class Point {
  x: number;
  y: number;

  constructor(x: number, y: number) {
    this.x = x;
    this.y = y;
  }
}

class Palette {
  colors: string[];
  isSelected: boolean;
  isVisible: boolean;

  constructor(colors: string[]) {
    this.colors = colors;
    this.isSelected = true;
    this.isVisible = true;
  }
}

const format_color = (rgb: number[]) => {
  const hex = (n: number) => ("0" + n.toString(16)).substr(-2);
  return (
    "#" +
    hex(Math.round(rgb[0] * 255)) +
    hex(Math.round(rgb[1] * 255)) +
    hex(Math.round(rgb[2] * 255))
  );
};

const hsv2rgb = (h: number, s: number, v: number) => {
  if (h < 0 || s < 0 || v < 0 || h > 1 || s > 1 || v > 1) return [0, 0, 0];

  if (h === 1.0) h = 0;

  const m = v - s; // min(r, g, b)
  const base = [
    [v, m, m],
    [v, v, m],
    [m, v, m],
    [m, v, v],
    [m, m, v],
    [v, m, v],
  ];
  const tbl = [
    [1, 1],
    [0, -1],
    [2, 1],
    [1, -1],
    [0, 1],
    [2, -1],
  ];
  const pos = Math.floor(h * 6);
  // if (pos > 5) console.log(h, pos);
  const i = tbl[pos][0];
  const dir = tbl[pos][1];

  const rgb = base[pos];
  rgb[i] += s * (h - pos / 6.0) * 6.0 * dir;

  return rgb;
};

const getContext = (id: string) => {
  const canvas: any = document.getElementById(id);
  if (canvas) return canvas.getContext("2d");
  return null;
};

interface ColorMapProps {
  width: number;
  height: number;
}

export const ColorMap: React.FC<ColorMapProps> = (props) => {
  const drawColorMap = () => {
    // console.log("drawColorMap");
    const ctx = getContext("colormap");

    const drawBar = (y: number, height: number, s: number, v: number) => {
      //console.log(s);
      const width = props.width;
      for (let x = 0; x < width; ++x) {
        var h = (x * 1.0) / width;
        // if (h >= 1.0) h -= 1.0;
        const rgb = hsv2rgb(h, s, v);
        ctx.fillStyle = format_color(rgb);
        ctx.fillRect(x, y, 1, height);
      }
    };

    const height = props.height;
    const dh = 1;
    const margin = 0;

    /*
    const v = 1.0;
    const ds = v / height;
    for (let y = 0; y < height; ++y) {
      drawBar((dh + margin) * y, dh, v - y * ds, v);
    }
     */

    const dv = 1.0 / height;
    for (let y = 0; y < height; ++y) {
      const v = 1.0 - dv * y;
      drawBar((dh + margin) * y, dh, v, v);
    }

    ctx.save();
  }; // drawColorMap

  useEffect(() => {
    drawColorMap();
  }, []);

  return (
    <canvas
      id="colormap"
      width={props.width}
      height={props.height}
      style={{ position: "absolute", zIndex: 1 }}
    />
  );
};

export interface ColorSelectorProps {
  width: number;
  height: number;
  ncolors: number;
  setColors: (colors: string[]) => void;
  onClick: (colors: string[]) => void;
  onMouseLeave: () => void;
}

export const ColorSelector: React.FC<ColorSelectorProps> = (props) => {
  const pos2hsv = (x: number, y: number) => {
    const h = (x * 1.0) / props.width;
    if (false) {
      const v = 1.0;
      const s = v - (v / props.height) * y;
      return [h, s, v];
    } else {
      const v = 1.0 - (1.0 / props.height) * y;
      const s = v;
      return [h, s, v];
    }
  };

  const drawCircle = (
    ctx: any,
    x: number,
    y: number,
    r: number,
    no: number
  ) => {
    var circle = new Path2D();
    circle.moveTo(x + r, y);
    circle.arc(x, y, r, 0, 2 * Math.PI);
    if (ctx) {
      // console.log("drawCircle: ", x, y, r);
      // ctx.strokeStyle = '#000000';
      ctx.strokeStyle = "#ffffff";
      //ctx.stroke(circle);
      ctx.fillStyle = "#ffffff";
      ctx.font = "14px sans-serif";
      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      ctx.fillText("" + no, x, y);
      ctx.save();
    }
  };

  const drawGuide = (points: Point[], r: number) => {
    const ctx = getContext("overlay");
    ctx.clearRect(0, 0, props.width, props.height);
    for (let i = 0; i < points.length; ++i) {
      const point = points[i];
      drawCircle(ctx, point.x, point.y, r, i);
    }
  };

  const calcColors = (nx: number, ny: number) => {
    const points: Point[] = [];

    //const x0 = n.offsetX % (props.width / props.ncolors);
    const x0 = nx;

    const dx = props.width / props.ncolors;
    const dy = 0; // props.height / props.ncolors;
    for (let i = 0; i < props.ncolors; ++i) {
      //const x = (n.offsetX + dx * i) % props.width;
      const x = (x0 + dx * i) % props.width;
      const y = (ny + dy * i) % props.height;
      points.push(new Point(x, y));
    }
    /*
    const dx = props.width / props.ncolors;
    const dy = 0; // props.height / props.ncolors;
    for (let i = 0; i < props.ncolors; ++i) {
      const j = Math.floor(i / 2) + Math.ceil(props.ncolors / 2) * (i % 2);
      const x = (x0 + dx * j) % props.width;
      const y = (n.offsetY + dy * i) % props.height;
      points.push(new Point(x, y));
    }
     */

    var colors: string[] = [];
    for (let point of points) {
      const hsv = pos2hsv(point.x, point.y);
      colors.push(format_color(hsv2rgb(hsv[0], hsv[1], hsv[2])));
    }

    return colors;
  };

  const onMouseMove = (event: any) => {
    const n = event.nativeEvent;

    const points: Point[] = [];

    //const x0 = n.offsetX % (props.width / props.ncolors);
    const x0 = n.offsetX;

    const dx = props.width / props.ncolors;
    const dy = 0; // props.height / props.ncolors;
    for (let i = 0; i < props.ncolors; ++i) {
      //const x = (n.offsetX + dx * i) % props.width;
      const x = (x0 + dx * i) % props.width;
      const y = (n.offsetY + dy * i) % props.height;
      points.push(new Point(x, y));
    }
    /*
    const dx = props.width / props.ncolors;
    const dy = 0; // props.height / props.ncolors;
    for (let i = 0; i < props.ncolors; ++i) {
      const j = Math.floor(i / 2) + Math.ceil(props.ncolors / 2) * (i % 2);
      const x = (x0 + dx * j) % props.width;
      const y = (n.offsetY + dy * i) % props.height;
      points.push(new Point(x, y));
    }
     */

    drawGuide(points, 4);

    var colors: string[] = [];
    for (let point of points) {
      const hsv = pos2hsv(point.x, point.y);
      colors.push(format_color(hsv2rgb(hsv[0], hsv[1], hsv[2])));
    }
    props.setColors(colors);
  };

  const onClick = (event: any) => {
    // console.log("onclick");
    const colors = calcColors(
      event.nativeEvent.offsetX,
      event.nativeEvent.offsetY
    );
    props.onClick(colors);
  };

  return (
    <div>
      {/* X axis */}
      <div style={{ width: props.width, textAlign: "center" }}>Hue</div>
      <div
        style={{
          width: props.width,
          height: 20,
          position: "relative",
          fontSize: "x-small",
          marginLeft: 0,
          textAlign: "center",
        }}
      >
        <div style={{ position: "absolute", textAlign: "left" }}>0</div>
        <div
          style={{
            position: "absolute",
            left: props.width / 6 - 10,
            width: 20,
          }}
        >
          60
        </div>
        <div
          style={{
            position: "absolute",
            left: (props.width / 6) * 2 - 10,
            width: 20,
          }}
        >
          120
        </div>
        <div
          style={{
            position: "absolute",
            left: (props.width / 6) * 3 - 10,
            width: 20,
          }}
        >
          180
        </div>
        <div
          style={{
            position: "absolute",
            left: (props.width / 6) * 4 - 10,
            width: 20,
          }}
        >
          240
        </div>
        <div
          style={{
            position: "absolute",
            left: (props.width / 6) * 5 - 10,
            width: 20,
          }}
        >
          300
        </div>
        <div
          style={{
            position: "absolute",
            left: (props.width / 6) * 6 - 80,
            width: 80,
            textAlign: "right",
          }}
        >
          360
        </div>
      </div>

      <div
        style={{
          width: props.width,
          height: props.height,
          marginLeft: 0,
          position: "relative",
        }}
      >
        <ColorMap width={props.width} height={props.height} />
        <canvas
          id="overlay"
          style={{ position: "absolute", zIndex: 2 }}
          width={props.width}
          height={props.height}
          onMouseMove={onMouseMove}
          onMouseLeave={props.onMouseLeave}
          onClick={onClick}
        />
      </div>
    </div>
  );
};

interface SampleChartProps {
  colors: string[];
}

// {x: "x0", "data0": 1.234, "data1": 3.14, ...}
const createSampleData = (n: number, nx: number) => {
  const data: { [label: string]: number | string }[] = [];
  for (let x = 0; x < nx; ++x) {
    const tmp: { [label: string]: number | string } = { x: 2018 + x };
    for (let i = 0; i < n; ++i) {
      const label = "data" + i;
      tmp[label] = Math.floor(Math.random() * 100);
    }
    data.push(tmp);
  }
  return data;
};

const getSampleData = (function () {
  const sample_data: { [n: number]: any } = {};

  return (n: number, nx: number) => {
    const key = n * nx;
    if (!(key in sample_data)) sample_data[key] = createSampleData(n, nx);
    return sample_data[key];
  };
})();

export const SampleBarChart: React.FC<SampleChartProps> = (props) => {
  const data = getSampleData(props.colors.length, 4);

  const bars: JSX.Element[] = [];

  for (let i = 0; i < props.colors.length; ++i) {
    bars.push(
      <Bar
        key={"bar" + i}
        dataKey={"data" + i}
        fill={props.colors[i]}
        isAnimationActive={false}
      />
    );
  }

  return (
    <ResponsiveContainer width="90%" height={240}>
      <BarChart data={data}>
        <XAxis dataKey="x" />
        <YAxis />
        <Legend />
        {bars}
      </BarChart>
    </ResponsiveContainer>
  );
};

const createPieData = (function () {
  const store: { [n: number]: any } = {};

  return (n: number) => {
    if (n in store) return store[n];

    const data: { i: number; data: number }[] = [];
    for (let i = 0; i < n; ++i) {
      data.push({ i: i, data: Math.floor(Math.random() * 100) });
    }
    store[n] = data;

    return data;
  };
})();

export const SamplePieChart: React.FC<SampleChartProps> = (props) => {
  const data = createPieData(props.colors.length);

  const cells: JSX.Element[] = [];

  for (let i = 0; i < props.colors.length; ++i) {
    cells.push(<Cell key={"cell" + i} fill={props.colors[i]} />);
  }

  return (
    <ResponsiveContainer width="50%" height={240}>
      <PieChart height={240}>
        <Pie
          data={data}
          dataKey={"data"}
          label={true}
          outerRadius={"70%"}
          isAnimationActive={false}
        >
          {cells}
        </Pie>
      </PieChart>
    </ResponsiveContainer>
  );
};

export const SampleLineChart: React.FC<SampleChartProps> = (props) => {
  const data = getSampleData(props.colors.length, 8);

  const lines: JSX.Element[] = [];

  for (let i = 0; i < props.colors.length; ++i) {
    lines.push(
      <Line
        key={"line" + i}
        dataKey={"data" + i}
        fill={props.colors[i]}
        stroke={props.colors[i]}
        isAnimationActive={false}
      />
    );
  }

  return (
    <ResponsiveContainer width="90%" height={240}>
      <LineChart
          data={data}
        >
          <XAxis dataKey="x" />
          <YAxis />
          { lines }
      </LineChart>
    </ResponsiveContainer>
  );
};

export interface ColorSampleProps {
  width: number;
  colors: string[];
}

export const ColorSample: React.FC<ColorSampleProps> = (props) => {
  const colors = props.colors;
  let width = (props.width - 10 * (colors.length - 1)) / colors.length;

  const baseStyle = {
    width: width,
    height: 20,
    marginRight: 10,
    fontSize: "x-small",
    textAlign: "center" as const,
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column" as const,
  };

  const boxes: JSX.Element[] = [];
  const texts: JSX.Element[] = [];
  for (let i = 0; i < props.colors.length; ++i) {
    boxes.push(
      <div
        key={"sample-box" + i}
        style={{ ...baseStyle, background: props.colors[i], color: "#ffffff" }}
      >
        {i}
      </div>
    );
    texts.push(
      <div
        key={"sample-text" + i}
        style={{
          ...baseStyle,
          color: props.colors[i],
          height: 10,
          marginTop: 2,
        }}
      >
        {props.colors[i]}
      </div>
    );
  }

  const textDiv = (
    <div style={{ display: "flex", flexDirection: "row", marginTop: 0 }}>
      {texts}
    </div>
  );

  return (
    <div>
      <div style={{ display: "flex", flexDirection: "row", margin: 0 }}>
        {boxes}
      </div>
      {textDiv}
    </div>
  );
};

interface ControlProps {
  width: number;
  ncolors: number;
  setNumColors: (ncolors: number) => void;
  backgroundColor: string;
  setBackgroundColor: (color: string) => void;
}

const Control: React.FC<ControlProps> = (props) => {
  //const [bgcolor, setBgcolor] = useState<string>(props.backgroundColor);

  const onChange = (e: React.ChangeEvent<{ value: unknown }>) => {
    props.setNumColors(e.target.value as number);
  };

  const onBackGroundChange = (
    e: React.MouseEvent<HTMLElement>,
    value: string
  ) => {
    props.setBackgroundColor(value);
    //setBgcolor(value);
  };

  return (
    <div style={{ width: props.width }}>
      <Grid container spacing={2} direction="column">
        <Grid item>
          <FormControl size="small">
            <FormLabel style={{ fontSize: "small" }}># of colors</FormLabel>
            <Select
              value={props.ncolors}
              onChange={onChange}
              style={{ textAlign: "center", marginTop: 8 }}
            >
              <MenuItem value={2}>2</MenuItem>
              <MenuItem value={3}>3</MenuItem>
              <MenuItem value={4}>4</MenuItem>
              <MenuItem value={5}>5</MenuItem>
              <MenuItem value={6}>6</MenuItem>
              <MenuItem value={7}>7</MenuItem>
              <MenuItem value={8}>8</MenuItem>
              <MenuItem value={9}>9</MenuItem>
              <MenuItem value={10}>10</MenuItem>
              <MenuItem value={11}>11</MenuItem>
              <MenuItem value={12}>12</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item>
          <FormControl size="small">
            <FormLabel style={{ fontSize: "small" }}>Background</FormLabel>
            <ToggleButtonGroup
              value={props.backgroundColor}
              exclusive
              onChange={onBackGroundChange}
              size="small"
            >
              <ToggleButton value="#ffffff">Light</ToggleButton>
              <ToggleButton value="#111111">Dark</ToggleButton>
            </ToggleButtonGroup>
          </FormControl>
        </Grid>
      </Grid>
    </div>
  );
};

interface PaletteItemProps {
  palette: Palette;
  selectPalette: (palette: Palette) => void;
}

const PaletteItem: React.FC<PaletteItemProps> = (props) => {
  const [showBorder, setShowBorder] = useState(false);
  const [hide, setHide] = useState(false);

  const onClick = (ev: any) => {
    // console.log("onClick");
    props.selectPalette(props.palette);
  };

  const onMouseEnter = (ev: any) => {
    // console.log("onMouseEnter");
    setShowBorder(true);
  };

  const onMouseLeave = (ev: any) => {
    // console.log("onMouseEnter");
    setShowBorder(false);
  };

  const removePalette = () => {
    setHide(true);
  };

  const boxes: JSX.Element[] = [];
  for (let i = 0; i < props.palette.colors.length; ++i) {
    boxes.push(
      <div
        key={"palette-box" + i}
        style={{
          width: 20,
          height: 20,
          background: props.palette.colors[i],
          marginRight: i + 1 < props.palette.colors.length ? 2 : 0,
          border: "solid 1px #444444",
        }}
      ></div>
    );
  }

  let border = { border: "solid 1px #ffffff", padding: 3 };
  if (props.palette.isSelected) {
    border = { border: "solid 2px #000000", padding: 2 };
  } else if (showBorder) {
    border = { border: "solid 1px #000000", padding: 3 };
  }

  let display = { display: "flex" };
  if (hide) display = { display: "none" };

  return (
    <div
      style={{
        ...display,
        textAlign: "center" as const,
        //justifyContent: "center",
        alignContent: "center",
        alignItems: "center",
        flexDirection: "row" as const,
      }}
    >
      <div
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        style={{
          display: "flex",
          flexDirection: "row",
          ...border,
          cursor: "pointer",
        }}
        onClick={onClick}
      >
        {boxes}
      </div>
      <RemoveCircleOutlineIcon
        fontSize="small"
        style={{ cursor: "pointer" }}
        onClick={removePalette}
      />
    </div>
  );
};

interface BottomPartProps {
  width: number;
  ncolors: number;
  paletteList: Palette[];
  currentColors: string[];
  onPaletteSelected: (palette: Palette) => void;
  clearPaletteList: () => void;
  bgcolor: string;
}

const BottomPart: React.FC<BottomPartProps> = (props) => {
  const items: JSX.Element[] = [];
  // console.log("BottomPart");

  let i = 0;
  for (let palette of props.paletteList) {
    // console.log('BottomPart', i, palette.isVisible);
    if (palette.isVisible) {
      items.push(
        <div key={"palette" + i} style={{ marginBottom: 1 }}>
          <PaletteItem
            palette={palette}
            selectPalette={(palette_: Palette) => {
              props.onPaletteSelected(palette_);
              for (let p of props.paletteList) {
                if (p === palette) {
                  p.isSelected = true;
                } else {
                  p.isSelected = false;
                }
              }
            }}
          />
        </div>
      );
    }
    ++i;
  }

  let tmp = "";
  for (let palette of props.paletteList) {
    if (palette.isSelected) {
      tmp = palette.colors.join("\n");
    }
  }

  return (
    <Grid container>
      <Grid item container style={{ marginTop: 10 }}>
        <Grid item>
          <ColorSample width={props.width} colors={props.currentColors} />
        </Grid>
      </Grid>

      <Grid item container>
        <Grid item xs={8}>
          <div
            style={{
              background: props.bgcolor,
              border: "solid 1px",
              marginTop: "10px",
              marginRight: "20px",
              padding: "5px",
            }}
          >
            <h4
              style={{
                border: "none 1px",
                margin: "0px",
                marginBottom: "20px",
                padding: "0px",
              }}
            >
              Sample Charts (Bar, Pie, Line)
            </h4>
            <SampleBarChart colors={props.currentColors} />
            <SamplePieChart colors={props.currentColors} />
            <SampleLineChart colors={props.currentColors} />
          </div>
        </Grid>

        <Grid item>
          <Grid container alignItems="center" spacing={2}>
            <Grid item>
              <h4>Palette List</h4>
            </Grid>
            <Grid item>
              <Button
                variant="outlined"
                size="small"
                onClick={props.clearPaletteList}
              >
                Clear all
              </Button>
            </Grid>
          </Grid>
          {items}

          <h4>Selected Palette</h4>
          <pre style={{ fontSize: "large" }}>{tmp}</pre>
        </Grid>
      </Grid>
    </Grid>
  );
};

export interface PaletteMakerProps {
  width: number;
  height: number;
}

export const PaletteMaker: React.FC<PaletteMakerProps> = (props) => {
  const [ncolors, setNumColors] = useState<number>(6);
  const [bgcolor, setBackgroundColor] = useState<string>("#ffffff");

  const [paletteList, setPaletteList] = useState<Palette[]>([]);

  const [currentColors, setColors] = useState<string[]>(
    Array(ncolors).fill("#ffffff")
  );

  const addPalette = (colors: string[]) => {
    const newPaletteList = [...paletteList];
    for (let p of newPaletteList) p.isSelected = false;
    newPaletteList.push(new Palette(colors));
    setPaletteList(newPaletteList);
  };

  const clearPaletteList = () => {
    const newPaletteList = [...paletteList];
    for (let p of newPaletteList) p.isVisible = false;
    setPaletteList(newPaletteList);
  };

  const selectPalette = (palette: Palette) => {
    setColors(palette.colors);
  };

  const resetColors = () => {
    const p = paletteList.find((p) => p.isSelected);
    if (p) setColors(p.colors);
  };

  //<div style={{width: props.width}}>
  return (
    <Grid container justify="center" style={{ background: "#bbbbbb" }}>
      <Grid
        item
        container
        style={{
          width: props.width + 20,
          border: "solid 1px",
          background: "white",
          padding: "10px",
        }}
      >
        <h1
          style={{ marginTop: "0px", marginBottom: "0px", fontSize: "large" }}
        >
          Color Palette Generator for Charts
        </h1>
        <Grid item container direction="column" style={{ width: props.width }}>
          <Grid item>
            <ColorSelector
              width={props.width}
              height={props.height}
              ncolors={ncolors}
              setColors={setColors}
              onClick={addPalette}
              onMouseLeave={resetColors}
            />
          </Grid>

          <Grid item style={{ padding: 0, width: props.width }}>
            <BottomPart
              width={props.width}
              ncolors={ncolors}
              paletteList={paletteList}
              currentColors={currentColors}
              onPaletteSelected={selectPalette}
              clearPaletteList={clearPaletteList}
              bgcolor={bgcolor}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid
        item
        style={{
          background: "white",
          width: "200px",
          marginLeft: "3px",
          paddingLeft: "5px",
          paddingRight: "5px",
          border: "solid 1px",
        }}
      >
        <h4 className="myh4">About</h4>
        <p>
          {" "}
          You can generate constant hue strided palettes while looking at
          various sample charts.
        </p>
        <h4 className="myh4">How to use</h4>
        <p>
          <ol style={{ paddingLeft: "24px" }}>
            <li>Move a pointer on color map to find a palette you like.</li>
            <li>Click to save a current palette into a palette list.</li>
            <li>Color code of current palette is shown under the list.</li>
          </ol>
        </p>

        <h4 className="myh4">Control</h4>
        <p>
          <Control
            width={props.width}
            backgroundColor={bgcolor}
            setBackgroundColor={setBackgroundColor}
            ncolors={ncolors}
            setNumColors={setNumColors}
          />
        </p>

        <p>
          <a href="https://github.com/iszk1215/palette">
            <img
              src="https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png"
              width="28px"
              style={{ verticalAlign: "middle" }}
            />
            <span style={{ verticalAlign: "middle" }}> Feedback </span>
          </a>
        </p>
        <p style={{ fontSize: "small" }}>Jun 30, 2021</p>
      </Grid>
    </Grid>
  );
};
